.header {
  margin-bottom: 1rem;
}

.section {
  background-color: #eeeeee;
  width: 100%;
  height: auto;
  padding: 1rem;
}

.close-button {
  border: 0;
  background-color: white;
}

.modal-header {
  border: 0;
}

.modal-body {
  padding-top: 0;
}

@media (max-width: 576px) {
  h1 {
    font-size: 1.7rem;
  }
}

.progress {
  height: 1px;
}

.progress-bar {
  width: 25%;
}

